import React, { PureComponent } from "react";
import PropTypes from "prop-types";
import { DisplayText } from "../../atoms";
import { composeClassName } from "../../utilities";
import "./NavigationColumn.scss";

class NavigationColumn extends PureComponent {
  static propTypes = {
    className: PropTypes.string,
    /** The title at the top of the column */
    title: PropTypes.string.isRequired,
    /** Only text BaseLinks should be passed into this component */
    children: PropTypes.array.isRequired,
  };

  render() {
    const { className, title, children } = this.props;
    const composedClassName = composeClassName(["NavigationColumn", className]);
    const linkList = children.map((node) => {
      const linkNode = { ...node };
      linkNode.props = { ...node.props, tabIndex: "0" };
      // This uses the link text as the unique key for the list.
      return (
        <li className="NavigationColumnItem" key={node.props.children}>
          {linkNode}
        </li>
      );
    });

    return (
      <div className={composedClassName}>
        <DisplayText className="NavigationColumnTitle" size="M">
          {title}
        </DisplayText>
        <ul>{linkList}</ul>
      </div>
    );
  }
}

export default NavigationColumn;
