import React, { PureComponent } from "react";
import PropTypes from "prop-types";

import { Label, Icon, BodyText } from "../../../atoms";
import { composeClassName } from "../../../utilities";

import asFormElement from "../../asFormElement";
import { FormContext } from "../../Form";


import "./Input.scss";

class Input extends PureComponent {
  static propTypes = {
    /** Indicates if the user input is accepted */
    approved: PropTypes.bool,
    className: PropTypes.string,
    disabled: PropTypes.bool,
    /** Indicates if there is an error in the user input */
    error: PropTypes.bool,
    icon: PropTypes.string,
    label: PropTypes.string.isRequired,
    name: PropTypes.string.isRequired,
    onChangeHandler: PropTypes.func,
    placeholder: PropTypes.string,
    /** Indicate purpose of text input for accessibility */
    title: PropTypes.string.isRequired,
    value: PropTypes.string,
    initialValue: PropTypes.string,
    type: PropTypes.oneOf(["text", "email", "hidden"]),
    /** Shorthand for required.
     * String or Function that returns a string is the error message displayed
     * If boolean, default error message is used. */
    required: PropTypes.oneOfType([
      PropTypes.func,
      PropTypes.string,
      PropTypes.bool,
    ]),
    emptyValue: PropTypes.any,
    /** Defined keys are validated against using standard validators.
     * String or Function that returns a string is the error message displayed
     * If boolean, default error message is used. */
    validateAgainst: PropTypes.object,
  };

  static defaultProps = {
    approved: false,
    className: "",
    disabled: false,
    error: false,
    icon: "",
    placeholder: "",
    initialValue: "",
    emptyValue: "",
  };

  _onChange = (event) => {
    event.preventDefault();
    const { target } = event;
    const { value } = target;
    const { name } = this.props;
    this.props.onChangeHandler({ name, value }); // eslint-disable-line
  };

  render() {
    const {
      approved,
      className,
      disabled,
      error,
      icon,
      label,
      name,
      placeholder,
      title,
      value,
      message,
      type,
      whiteText,
    } = this.props;

    const labelProps = {
      label,
      whiteText,
    };

    const composedClassName = composeClassName([
      "Input",
      approved ? "approved" : "",
      error ? "error" : "",
      className,
    ]);

    let iconComponent = null;
    let noIconStyles = { width: "100%" };
    if (icon) {
      const iconProps = { icon };
      noIconStyles = null;
      iconComponent = <Icon size="S" {...iconProps} />;
    }

    if (error) {
      noIconStyles = null;
      iconComponent = <Icon size="S" icon="close" />;
    }

    if (approved) {
      noIconStyles = null;
      iconComponent = <Icon size="S" icon="checkmark" />;
    }

    const htmlFor = name;
    const inputGroupClassName = composeClassName([
      "InputGroup",
      approved ? "approved" : "",
      error ? "error" : "",
    ]);
    return (
      <div className="InputContainer">
        <Label htmlFor={htmlFor} {...labelProps}>
          <div className={inputGroupClassName}>
            <input
              className={composedClassName}
              disabled={disabled}
              id={htmlFor}
              name={name}
              onChange={this._onChange}
              placeholder={placeholder}
              style={noIconStyles}
              title={title}
              type={type}
              value={value}
            />
            {iconComponent}
          </div>
          {/* TODO: Turn into proper error message. */}
          <BodyText size="S" className="ErrorMessage" whiteText={whiteText}>
            {message}
          </BodyText>
        </Label>
      </div>
    );
  }
}

export default asFormElement(Input, FormContext);
