import React, { PureComponent } from "react";
import PropTypes from "prop-types";
import { composeClassName } from "../../utilities";
import { BaseLink, BodyText, Image } from "../../atoms";
import LocaleSwitcher from "../../molecules/LocaleSwitcher";
import { allowedLocales } from "../../../config";

import "./Footer.scss";

const weLogoSource = "https://static.we.org/logos/WE_RGB_blue_June2016.png";

class Footer extends PureComponent {
  static propTypes = {
    className: PropTypes.string,
    columns: PropTypes.array,
    socialButtons: PropTypes.array,
    noticeToChildrenHref: PropTypes.string,
    noticeToChildrenExternal: PropTypes.bool,
    copyrightInfo: PropTypes.string,
    bottomLinks: PropTypes.array,
    internalLinkComponent: PropTypes.any.isRequired,
  };

  static defaultProps = {
    className: "",
    columns: [],
    socialButtons: [],
    noticeToChildrenHref: "",
    noticeToChildrenExternal: false,
    copyrightInfo: "",
    bottomLinks: [],
  };

  render() {
    const {
      className,
      columns,
      socialButtons,
      noticeToChildrenHref,
      noticeToChildrenExternal,
      copyrightInfo,
      bottomLinks,
      internalLinkComponent,
    } = this.props;

    const composedClassName = composeClassName(["Footer", className]);

    const logo = (
      <div className="Logo">
        <Image source={weLogoSource} altText="" />
      </div>
    );

    const bottomLinkObjects = bottomLinks.reduce((objects, link) => {
      return [
        ...objects,
        link,
        <span className="BaseLinkSeparator" key={`${link.key}-after`}>
          {" | "}
        </span>,
      ];
    }, []);
    bottomLinkObjects.pop();

    return (
      <footer className={composedClassName}>
        <div className="FooterContent">
          {logo}
          <div className="FooterColumns">{columns}</div>
          <div className="FooterDividerSection">
            <div className="FooterSocialButtons">{socialButtons}</div>
            <BaseLink
              className="NoticeToChildren"
              internalLinkComponent={internalLinkComponent}
              href={noticeToChildrenHref}
              external={noticeToChildrenExternal}>
              Notice to Children Under 13
            </BaseLink>
          </div>
          <div>
            <BodyText size="M" className="Copyright">
              {copyrightInfo}
            </BodyText>
            <LocaleSwitcher allowedLocales={allowedLocales} />
          </div>
          <div className="FooterBottomLinks">{bottomLinkObjects}</div>
        </div>
      </footer>
    );
  }
}

export default Footer;
