import React, { PureComponent } from "react";

import PropTypes from "prop-types";

import { ModalWrapper, VideoPlayer } from "../../atoms";
import ButtonVideo from "../ButtonVideo";
import ButtonCloseModal from "../ButtonCloseModal";

import "./ModalVideo.scss";

class ModalVideo extends PureComponent {
  static propTypes = {
    title: PropTypes.string.isRequired,
    videoId: PropTypes.string.isRequired,
    allowFullScreen: PropTypes.bool,
    className: PropTypes.string,
    noDisplayText: PropTypes.bool,
    source: PropTypes.oneOf(["vimeo", "youtube"]),
  };

  constructor(props) {
    super(props);
    this.state = { open: false };
    this.closeButton = React.createRef();
    this.openButton = React.createRef();
  }

  _handleOpenModal = () => {
    this.setState({ open: true }, () => {
      this.closeButton.current.focus();
    });
  };

  _handleCloseModal = () => {
    this.setState({ open: false }, () => {
      this.openButton.current.focus();
    });
  };

  render() {
    const {
      allowFullScreen,
      className,
      source,
      title,
      videoId,
      theme,
      noDisplayText,
    } = this.props;
    const { open } = this.state;

    return (
      <div className={className}>
        <ButtonVideo
          ariaLabel="Modal Open"
          buttonRef={this.openButton}
          className="ModalVideoOpen"
          noDisplayText={noDisplayText}
          onClickHandler={this._handleOpenModal}
          theme={theme}
        />
        <ModalWrapper
          open={open}
          closeHandler={this._handleCloseModal}
          ariaLabel="Video Modal"
          ariaDescribedBy="Open video in a modal">
          <VideoPlayer
            allowFullScreen={allowFullScreen}
            source={source}
            title={title}
            videoId={videoId}
            autoplay
          />
          <ButtonCloseModal
            ariaLabel="Modal Close"
            buttonRef={this.closeButton}
            className="ModalVideoClose"
            onClickHandler={this._handleCloseModal}
          />
        </ModalWrapper>
      </div>
    );
  }
}

export default ModalVideo;
