import React, { PureComponent } from "react";
import PropTypes from "prop-types";

import { Label, Icon } from "../../atoms";
import { composeClassName } from "../../utilities";

import "./InputTextArea.scss";

class InputTextArea extends PureComponent {
  static propTypes = {
    /** Indicates if the user input is accepted */
    approved: PropTypes.bool,
    className: PropTypes.string,
    disabled: PropTypes.bool,
    /** Indicates if there is an error in the user input */
    error: PropTypes.bool,
    icon: PropTypes.string,
    label: PropTypes.string.isRequired,
    name: PropTypes.string.isRequired,
    onChangeHandler: PropTypes.func,
    placeholder: PropTypes.string,
    /** Indicate purpose of text input for accessibility */
    title: PropTypes.string.isRequired,
    value: PropTypes.string,
  };

  static defaultProps = {
    approved: false,
    className: "",
    disabled: false,
    error: false,
    icon: "",
    placeholder: "",
  };

  render() {
    const {
      approved,
      className,
      disabled,
      error,
      icon,
      label,
      name,
      onChangeHandler,
      placeholder,
      title,
      value,
    } = this.props;

    const labelProps = {
      label,
    };

    const composedClassName = composeClassName([
      "TextAreaGroup",
      approved ? "approved" : "",
      error ? "error" : "",
      className,
    ]);

    let iconComponent = null;
    let noIconStyles;
    if (icon) {
      const iconProps = { icon };
      noIconStyles = null;
      iconComponent = <Icon size="S" {...iconProps} />;
    }

    const htmlFor = name;

    return (
      <div className="TextAreaContainer">
        <Label htmlFor={htmlFor} {...labelProps}>
          <div className={composedClassName}>
            <textarea
              className="InputTextArea"
              id={htmlFor}
              disabled={disabled}
              name={name}
              onChange={onChangeHandler}
              placeholder={placeholder}
              style={noIconStyles}
              title={title}
              value={value}
            />
            {iconComponent}
          </div>
        </Label>
      </div>
    );
  }
}

export default InputTextArea;
