import React, { PureComponent } from "react";
import PropTypes from "prop-types";

import { composeClassName } from "../../utilities";
import browser from "../../utilities/browser";

import "./ImageBackground.scss";

class ImageBackground extends PureComponent {
  static propTypes = {
    /** The source file for the main image */
    source: PropTypes.string.isRequired,
    /** The source file for the mobile image */
    mobileSource: PropTypes.string,
    className: PropTypes.string,
  };

  static defaultProps = {
    circular: false,
    className: "",
  };

  render() {
    const { className, children, mobileSource, source } = this.props;
    const isMobile = browser.window.innerWidth < 768;

    const imageSource = isMobile && mobileSource ? mobileSource : source;

    const composedClassName = composeClassName(["ImageBackground", className]);

    return (
      <div
        className={composedClassName}
        style={{ backgroundImage: `url(${imageSource})` }}>
        {children}
      </div>
    );
  }
}

export default ImageBackground;
