import React, { PureComponent } from "react";
import PropTypes from "prop-types";
import { composeClassName } from "../../utilities";
import "./Icon.scss";
import IconPath from "./IconPaths.js"; // eslint-disable-line

class Icon extends PureComponent {
  static IconPath = IconPath;

  static propTypes = {
    className: PropTypes.string,
    /** The selection for the displayed icon */
    icon: PropTypes.oneOf(Object.keys(Icon.IconPath)).isRequired,
    /** The selection for the size of the icon */
    size: PropTypes.oneOf(["S", "M", "L"]).isRequired,
    /** The selection for the default color of the icon */
    defaultColor: PropTypes.oneOf(["DefaultGrey", "DefaultWhite"]),
    /** The description of the icon, defaults to the name of the icon */
    title: PropTypes.string,
    /** The role of the icon, defaults to the role of an image */
    role: PropTypes.string,
  };

  static defaultProps = {
    className: "",
    size: "M",
    defaultColor: "DefaultGrey",
    title: "",
    role: "img",
  };

  render() {
    const { icon, size, defaultColor, className, title, role } = this.props;
    const composedClassName = composeClassName([
      `Icon ${icon} ${size} ${defaultColor}`,
      title,
      role,
      className,
    ]);

    const path = Icon.IconPath[icon];
    const maskId = `icon-${icon}`;
    const maskLinkId = `#a-${icon}`;
    const maskUrlRef = `url(#icon-${icon})`;
    const iconLabel = `${icon}-label`;

    return (
      <span className={composedClassName}>
        <svg
          xmlns="http://www.w3.org/2000/svg"
          xmlnsXlink="http://www.w3.org/1999/xlink"
          className="IconSize"
          viewBox="0 0 16 16"
          role={role}
          aria-labelledby={iconLabel}>
          <title id={iconLabel}>{title || `${icon} icon`}</title>
          <defs>{path}</defs>
          <g fill="none" fillRule="evenodd">
            <mask id={maskId} fill="#fff">
              <use xlinkHref={maskLinkId} />
            </mask>
            <g mask={maskUrlRef}>
              <path className="ColoredBase" d="M0 0h16v16H0z" />
            </g>
          </g>
        </svg>
      </span>
    );
  }
}

export default Icon;
