import React, { PureComponent } from "react";
import PropTypes from "prop-types";
import { BaseLink, DisplayText } from "../../atoms";
import { composeClassName } from "../../utilities";

import "./NavigationSub.scss";

class NavigationSub extends PureComponent {
  static propTypes = {
    /** An array of the NavigationColumn molecule */
    isMobile: PropTypes.bool,
    items: PropTypes.arrayOf(
      PropTypes.shape({
        href: PropTypes.string.isRequired,
        title: PropTypes.string.isRequired,
        external: PropTypes.bool,
        label: PropTypes.string,
      }),
    ).isRequired,
    currentHref: PropTypes.string,
    internalLinkComponent: PropTypes.any,
  };

  static defaultProps = {
    isMobile: false,
    internalLinkComponent: null,
  };

  render() {
    const {
      className,
      items,
      internalLinkComponent,
      isMobile,
      currentHref,
    } = this.props;
    const navigationSubItems = [];

    navigationSubItems.push(
      items.map((item) => {
        const { external, href, label, title } = item;
        return (
          <div
            key={title}
            className={composeClassName([
              "NavigationSubItem",
              item.href === currentHref ? "Current" : "",
            ])}>
            <BaseLink
              href={href}
              external={external}
              internalLinkComponent={internalLinkComponent}
              className="NavigationSubItemLink">
              <DisplayText size="M">{label}</DisplayText>
            </BaseLink>
          </div>
        );
      }),
    );

    const navigationSubDesktop = (
      <div className="NavigationSubDesktop">{navigationSubItems}</div>
    );

    const navigationSubMobile = (
      <div className="NavigationSubMobile">{navigationSubItems}</div>
    );

    let navigationSub = navigationSubDesktop;
    if (isMobile) {
      navigationSub = navigationSubMobile;
    }

    return (
      <div className={composeClassName(["NavigationSub", className])}>
        {navigationSub}
      </div>
    );
  }
}

export default NavigationSub;
