import React, { PureComponent } from "react";
import PropTypes from "prop-types";
import { Heading, Button, DisplayText, BaseLink, Image } from "../../atoms";
import { TextWithBackground, ButtonLink } from "../../molecules";
import { composeClassName } from "../../utilities";
import "./LeadGenerator.scss";

class LeadGenerator extends PureComponent {
  static propTypes = {
    className: PropTypes.string,
    imageSource: PropTypes.string,
    imageLongDesc: PropTypes.string,
    mobileImageSource: PropTypes.string,
    imageAltText: PropTypes.string,
    /** The text title inside the text-with-background section */
    title: PropTypes.string.isRequired,
    buttonText: PropTypes.string.isRequired,
    buttonTheme: PropTypes.string,
    buttonHref: PropTypes.string.isRequired,
    buttonIsExternal: PropTypes.bool,
    displayText: PropTypes.string,
    contactLinkHref: PropTypes.string,
    contactLinkIsExternal: PropTypes.bool,
    contactLinkText: PropTypes.string,
    contactLinkTitle: PropTypes.string,
    /** The background color */
    color: PropTypes.string,
    whiteText: PropTypes.bool,
    internalLinkComponent: PropTypes.any.isRequired,
  };

  static defaultProps = {
    className: "",
    imageSource: null,
    imageAltText: null,
    buttonTheme: "Primary",
    buttonIsExternal: false,
    displayText: "",
    contactLinkHref: "",
    contactLinkIsExternal: false,
    contactLinkText: "",
    contactLinkTitle: null,
    color: "#ffffff",
    whiteText: false,
  };

  render() {
    const {
      className,
      imageSource,
      imageLongDesc,
      mobileImageSource,
      imageAltText,
      title,
      buttonText,
      buttonTheme,
      buttonHref,
      buttonIsExternal,
      displayText,
      contactLinkHref,
      contactLinkIsExternal,
      contactLinkText,
      contactLinkTitle,
      color,
      whiteText,
      internalLinkComponent,
    } = this.props;

    const withImage = !!(imageSource && imageAltText);

    const composedClassName = composeClassName([
      "LeadGenerator",
      withImage ? "WithImage" : "",
      className,
    ]);

    let image = null;
    if (withImage) {
      image = (
        <Image
          className="LeadImage"
          altText={imageAltText}
          source={imageSource}
          mobileSource={mobileImageSource}
          longdesc={imageLongDesc}
        />
      );
    }

    let contactLink;
    if (contactLinkText) {
      contactLink = (
        <BaseLink
          className="ContactLink"
          href={contactLinkHref}
          external={contactLinkIsExternal}
          title={contactLinkTitle}
          internalLinkComponent={internalLinkComponent}>
          {contactLinkText}
        </BaseLink>
      );
    } else {
      contactLink = <DisplayText size="S" className="ContactLink" />;
    }

    return (
      <div className={composedClassName}>
        {image}
        <TextWithBackground
          className="LeadTextSection"
          color={color}
          whiteText={whiteText}>
          <Heading centered className="LeadHeading" size="S">
            {title}
          </Heading>
          <ButtonLink
            size="L"
            theme={buttonTheme}
            href={buttonHref}
            external={buttonIsExternal}
            internalLinkComponent={internalLinkComponent}
            className="LeadCTA CTAButton"
          >
            {buttonText}
          </ButtonLink>
          <DisplayText className="LeadDisplayText" size="S">
            {displayText}
          </DisplayText>
          {contactLink}
        </TextWithBackground>
      </div>
    );
  }
}

export default LeadGenerator;
