import React, { PureComponent } from "react";
import PropTypes from "prop-types";
import { StringUtils } from "../../utilities";
import {
  Container,
  DisplayText,
  BodyText,
  Label as SharedLabel,
  Divider,
  Image,
} from "../../atoms";
import { InputDropdown } from "../../forms/fields";
import ButtonIcon from "../ButtonIcon";
import ButtonLink from "../ButtonLink";

import "./DownloadableResources.scss";

class DownloadableResources extends PureComponent {
  static propTypes = {
    title: PropTypes.string.isRequired,
    label: PropTypes.string.isRequired,
    options: PropTypes.arrayOf(
      PropTypes.shape({
        value: PropTypes.string,
        label: PropTypes.string,
        description: PropTypes.string,
      }),
    ).isRequired,
    source: PropTypes.string.isRequired,
    altText: PropTypes.string.isRequired,
  };

  constructor(props) {
    super(props);
    const { options } = props;
    this.state = {
      downloadContent: options.length > 0 ? options[0].value : "",
    };
  }

  handleOnChange = (event) => {
    this.setState({ downloadContent: event.target.value });
  };

  render() {
    const { title, label, options, source, altText } = this.props;
    const { downloadContent } = this.state;
    // Create valid id string out of title
    const id = title.replace(/\W/g, "_");

    const selectedOption = options.find((option) => {
      return option.value === downloadContent;
    });

    const formattedOptions = options.map((option) => {
      return { value: option.value, label: option.label };
    });

    const description = selectedOption ? selectedOption.description : "";

    return (
      <Container fluid className="DownloadableResources">
        <div className="DownloadContainer">
          <Divider />
          <div className="DownloadContent">
            <div className="DownloadInfo">
              <div>
                <Image source={source} altText={altText} />
              </div>
              <div className="DownloadDescription">
                <DisplayText size="L">
                  {StringUtils.formatMax(title, 75)}
                </DisplayText>
                <BodyText size="S">{description}</BodyText>
              </div>
            </div>
            <SharedLabel htmlFor={id} label={label} className="DownloadLabel">
              <div className="DownloadLinks">
                <InputDropdown
                  id={id}
                  options={formattedOptions}
                  name="downloadable-content"
                  onChangeHandler={this.handleOnChange}
                />
                <ButtonIcon
                  icon="download"
                  theme="Primary"
                  size="M"
                  className="DownloadButtonMobile"
                  external
                  download
                  href={downloadContent}
                />
                <ButtonLink
                  theme="Primary"
                  size="M"
                  className="DownloadButton"
                  external
                  download
                  href={downloadContent}>
                  Download
                </ButtonLink>
              </div>
            </SharedLabel>
          </div>
          <Divider />
        </div>
      </Container>
    );
  }
}

export default DownloadableResources;
