import React, { PureComponent } from "react";
import PropTypes from "prop-types";
import { CSSTransition } from "react-transition-group";
import {
  Heading,
  BaseLink,
  BodyText,
  DisplayText,
  Image,
  Icon,
} from "../../atoms";
import { NavigationItemMobile } from "../../molecules";
import { composeClassName, camelize } from "../../utilities";
import "./NavigationBarMobile.scss";

const weLogoSource = "https://static.we.org/logos/WE_RGB_blue_June2016.png";

const sectionPropTypes = PropTypes.shape({
  title: PropTypes.string.isRequired,
  subMenuTitle: PropTypes.string.isRequired,
  subMenuBodyText: PropTypes.string,
  buttonHidden: PropTypes.bool,
  buttonText: PropTypes.string,
  buttonHref: PropTypes.string,
  buttonIsExternal: PropTypes.bool,
  /** An array of the NavigationColumn molecule */
  columns: PropTypes.array.isRequired,
});

class NavigationBarMobile extends PureComponent {
  static propTypes = {
    className: PropTypes.string,
    /** Each section has the following props:
     *  `title`, `bodyText`, `buttonHidden`, `buttonText`,
     *  `buttonHref`, `buttonIsExternal`, `columns` */
    sectionsArray: PropTypes.arrayOf(sectionPropTypes).isRequired,
    internalLinkComponent: PropTypes.any.isRequired,
    externalLinkInfo: PropTypes.arrayOf(
      PropTypes.shape({
        label: PropTypes.string,
        href: PropTypes.string,
        external: PropTypes.bool,
        title: PropTypes.string,
      }),
    ),
  };

  static defaultProps = {
    className: "",
    externalLinkInfo: [],
    sectionsArray: [],
  };

  constructor(props) {
    super(props);
    this.state = {
      menu: null,
      menuOpen: false,
      subMenuOpen: false,
    };
  }

  _toggleMenu = () => {
    this.setState((prevState) => {
      if (prevState.subMenuOpen === true) {
        return { menuOpen: false, subMenuOpen: false };
      }
      if (prevState.menuOpen === false) {
        return { menuOpen: true, subMenuOpen: false };
      }
      return { menuOpen: false, subMenuOpen: false };
    });
  };

  _toggleSubMenu = (subMenuItem) => {
    this.setState((prevState) => {
      if (prevState.subMenuOpen === false) {
        return { menu: subMenuItem, subMenuOpen: true };
      }
      return { menu: subMenuItem, subMenuOpen: false };
    });
  };

  _backToMainMenu = () => {
    this.setState(() => {
      return { menuOpen: true, subMenuOpen: false };
    });
  };

  render() {
    const {
      className,
      sectionsArray,
      internalLinkComponent,
      externalLinkInfo,
    } = this.props;
    const { menuOpen, subMenuOpen, menu } = this.state;

    const composedClassName = composeClassName([
      "NavigationBarWrapper",
      "Mobile",
      className,
    ]);
    const logo = (
      <div className="Logo">
        <Image source={weLogoSource} altText="WE Logo" />
      </div>
    );

    const mainBar = (
      <nav className="NavigationBar">
        {logo}
        <BaseLink
          blank
          className="Hamburger"
          onClickHandler={() => {
            this._toggleMenu();
          }}
        >
          <Icon
            icon="hamburger"
            size="M"
            className="NavigationHamburger"
            defaultColor="DefaultGrey"
            title="Toggle Main menu"
            role="img"
          />
        </BaseLink>
      </nav>
    );

    // TODO: Translations
    const categoryLinkInfo = sectionsArray.map((section) => {
      return { item: camelize(section.title), label: section.title };
    });

    const categoryLinks = categoryLinkInfo.map((linkInfo, key) => {
      const itemKey = `categoryKey-${key}`;
      return (
        <NavigationItemMobile
          key={itemKey}
          itemKey={itemKey}
          onClickHandler={() => {
            this._toggleSubMenu(linkInfo.item);
          }}
        >
          {linkInfo.label}
        </NavigationItemMobile>
      );
    });

    const externalLinks = externalLinkInfo.map((linkInfo, key) => {
      const itemKey = `externalLinkKey-${key}`;
      return (
        <NavigationItemMobile
          key={itemKey}
          itemKey={itemKey}
          title={linkInfo.title}
          internalLinkComponent={internalLinkComponent}
          href={linkInfo.href}
          external={linkInfo.external}
        >
          {linkInfo.label}
        </NavigationItemMobile>
      );
    });

    const sections = {};
    sectionsArray.forEach((section) => {
      sections[camelize(section.title)] = section;
    });

    const currentSubmenuMenu = subMenuOpen;
    const section = sections[menu] || {};

    // TODO: String translation for "Menu"
    const mainMenu = (
      <CSSTransition
        classNames="MenuSlide"
        timeout={600}
        key="Menu"
        unmountOnExit
        in={menuOpen}
      >
        <div className="NavigationMenu">
          <div className="NavigationWrapper">
            <a className="SkipToContentLink" href="#endOfNavbar">
              Skip to Content
            </a>
            <Heading size="M" className="MenuHeading">
              Menu
            </Heading>
            <ul className="NavigationItems">
              {categoryLinks}
              {externalLinks}
            </ul>
          </div>
        </div>
      </CSSTransition>
    );

    const currentCategory = categoryLinkInfo.find((link) => {
      return link.item === menu;
    });

    // prettier-ignore
    const subMenuName = (undefined !== currentCategory) ? currentCategory.label : null;

    const menuButton = section.buttonHidden ? null : (
      <BaseLink
        tabIndex="-1"
        className="MenuDescriptionLink"
        internalLinkComponent={internalLinkComponent}
        href={section.buttonHref}
        external={section.buttonIsExternal}
      >
        {section.buttonText}
      </BaseLink>
    );
    // TODO: String translations for "Back"
    const subMenu = (
      <CSSTransition
        classNames="SubMenuSlide"
        timeout={600}
        key="SubMenu"
        unmountOnExit
        in={currentSubmenuMenu}
      >
        <div className="NavigationMenu">
          <div className="NavigationWrapper">
            <BaseLink
              blank
              className="NavigationBackButton"
              onClickHandler={() => {
                this._backToMainMenu();
              }}
            >
              <Icon
                icon="chevronLeft"
                size="S"
                className="NavigationItemCaret"
                defaultColor="DefaultGrey"
              />
              <DisplayText size="M" className="SubMenuBack">
                Back
              </DisplayText>
            </BaseLink>

            <div className="SubMenuDescription">
              <Heading size="M" className="MenuHeading">
                {subMenuName}
              </Heading>

              <Heading size="S" className="MenuTitle">
                {section.title}
              </Heading>

              <BodyText size="L" className="MenuBodyText">
                {section.bodyText}
              </BodyText>
              {menuButton}
            </div>
            {section.columns}
          </div>
        </div>
      </CSSTransition>
    );

    return (
      <div className={composedClassName}>
        {mainBar}
        {mainMenu}
        {subMenu}
        <div id="endOfNavbar" />
      </div>
    );
  }
}

export default NavigationBarMobile;
