/* eslint-disable react/button-has-type */
/* eslint-disable no-restricted-syntax */
import React, { PureComponent } from "react";
import PropTypes from "prop-types";
import { composeClassName } from "../../../utilities";

import asFormElement from "../../asFormElement";
import { FormContext } from "../../Form";

import "./InputDropdown.scss";

class InputDropdown extends PureComponent {
  static propTypes = {
    className: PropTypes.string,
    disabled: PropTypes.bool,
    error: PropTypes.bool,
    name: PropTypes.string.isRequired,
    onChangeHandler: PropTypes.func,
    placeholder: PropTypes.string,
    /** List of option object */
    options: PropTypes.arrayOf(
      PropTypes.shape({
        value: PropTypes.oneOfType([PropTypes.string, PropTypes.number])
          .isRequired,
        label: PropTypes.string.isRequired,
      }),
    ).isRequired,
    value: PropTypes.oneOfType([PropTypes.string, PropTypes.number]),
  };

  static defaultProps = {
    disabled: false,
    error: false,
  };

  _onChange = (event) => {
    event.preventDefault();
    const { onChangeHandler, name } = this.props;
    const { target } = event;
    const { value } = target;

    onChangeHandler({ name, value });
  }

  render() {
    const {
      className,
      disabled,
      error,
      name,
      value,
      options,
      placeholder,
    } = this.props;

    const composedClassName = composeClassName(["InputDropdown", className]);

    const optionItems = [];
    for (const option of options) {
      optionItems.push(
        <option
          className="DropdownItem"
          key={option.value}
          value={option.value}>
          {option.label}
        </option>,
      );
    }

    let optionPlaceholder;
    if (placeholder) {
      optionPlaceholder = (
        <option className="DropdownPlaceholder" default hidden value="">
          {placeholder}
        </option>
      );
    }

    return (
      <div className={composedClassName}>
        <select
          className={`DropdownSelect ${error ? "Error" : ""}`}
          name={name}
          disabled={disabled}
          // eslint-disable-next-line arrow-body-style
          onChange={this._onChange}
          value={value}>
          {optionPlaceholder}
          {optionItems}
        </select>
      </div>
    );
  }
}

export default asFormElement(InputDropdown, FormContext);
