import React, { PureComponent } from "react";
import PropTypes from "prop-types";
import { CSSTransition } from "react-transition-group";
import { Heading, BaseLink, BodyText, Button, Image } from "../../atoms";
import { composeClassName, camelize } from "../../utilities";
import "./NavigationBarDesktop.scss";

const weLogoSource = "https://static.we.org/logos/WE_RGB_blue_June2016.png";

const sectionPropTypes = PropTypes.shape({
  title: PropTypes.string.isRequired,
  subMenuTitle: PropTypes.string.isRequired,
  subMenuBodyText: PropTypes.string,
  buttonHidden: PropTypes.bool,
  buttonText: PropTypes.string,
  buttonHref: PropTypes.string,
  buttonIsExternal: PropTypes.bool,
  /** An array of the NavigationColumn molecule */
  columns: PropTypes.array.isRequired,
});

class NavigationBarDesktop extends PureComponent {
  static propTypes = {
    className: PropTypes.string,
    /** Each section has the following props:
     *  `title`, `bodyText`, `buttonHidden`, `buttonText`,
     *  `buttonHref`, `buttonIsExternal`, `columns` */
    sectionsArray: PropTypes.arrayOf(sectionPropTypes).isRequired,
    internalLinkComponent: PropTypes.any.isRequired,
    externalLinkInfo: PropTypes.arrayOf(
      PropTypes.shape({
        label: PropTypes.string,
        href: PropTypes.string,
        external: PropTypes.bool,
        title: PropTypes.string,
      }),
    ),
  };

  static defaultProps = {
    className: "",
    externalLinkInfo: [],
    sectionsArray: [],
  };

  constructor(props) {
    super(props);
    this.state = {
      /** The currently selected menu item. The current series of animations will end with this
       *  menu showing. It has the value `null` when the menu is closed.
       */
      menu: null,
      /** The menu item which is currently exiting, or is queued for exiting while the menu is
       *  entering. If the menu is in a non-transition state, this has the value `null`.
       */
      exiting: null,
      /** The menu item which is currently entering. If the menu is not entering, this has the
       *  value `null`. If both entering and exiting have values, the menu is entering, but will
       *  immediately exit when it finishes.
       */
      entering: null,
    };

    const { sectionsArray } = this.props;

    this.menuRefs = {};
    this.menuButtonRefs = {};

    sectionsArray.forEach((section) => {
      this.menuRefs[camelize(section.title)] = React.createRef();
      this.menuButtonRefs[camelize(section.title)] = React.createRef();
    });
  }

  // This is just to ensure that the menu ref is recreated when the title of the section is changed
  componentDidUpdate() {
    const { sectionsArray } = this.props;

    sectionsArray.forEach((section) => {
      this.menuRefs[camelize(section.title)] = React.createRef();
      this.menuButtonRefs[camelize(section.title)] = React.createRef();
    });
  }

  _handleOnKeyDown = (event) => {
    // close on Escape
    if (event.keyCode === 27) {
      const { menu } = this.state;
      if (menu !== null) {
        this._toggleMenu(menu);
      }
    }
  };

  _toggleMenu = (menu) => {
    let ref;
    this.setState(
      (prevState) => {
        if (prevState.menu === menu) {
          ref = this.menuButtonRefs[menu];
          return { menu: null, exiting: menu };
        }
        ref = this.menuRefs[menu];
        return { menu };
      },
      () => {
        ref.current.focus();
      },
    );
  };

  _menuOpen = (menu) => {
    this.setState({ entering: menu });
  };

  _menuOpened = () => {
    this.setState({ entering: null });
  };

  _menuClose = (menu) => {
    this.setState({ exiting: menu });
  };

  _menuClosed = () => {
    this.setState({ exiting: null });
  };

  _isMenuPresent = () => {
    const { menu, entering, exiting } = this.state;
    /** If the menu is currently opening, it will finish opening even if the user has
     *  pressed the button to close it. */
    if (entering) {
      return true;
    }
    /** If the menu is currently closing or has been set to close, it will finish closing
     *  even if the user has pressed the button to open it. */
    if (exiting) {
      return false;
    }
    /** Otherwise, return true if the current menu is non-null and false if it is null. */
    return !!menu;
  };

  render() {
    const {
      className,
      sectionsArray,
      internalLinkComponent,
      externalLinkInfo,
    } = this.props;
    const { menu, exiting, entering } = this.state;

    const composedClassName = composeClassName([
      "NavigationBarWrapper",
      "Desktop",
      className,
    ]);
    const logo = (
      <div className="Logo">
        <Image source={weLogoSource} altText="" />
      </div>
    );

    const categoryLinkInfo = sectionsArray.map((section) => {
      return { item: camelize(section.title), label: section.title };
    });

    const sections = {};
    sectionsArray.forEach((section) => {
      sections[camelize(section.title)] = section;
    });

    const categoryLinks = categoryLinkInfo.map((linkInfo, index) => {
      const expanded = menu === linkInfo.item;
      const key = `category-${index}-${linkInfo.label}`;
      return (
        <div
          key={key}
          className={`CategoryLink${expanded ? " open" : ""}`}
        >
          <BaseLink
            blank
            a11yProps={{
              "aria-expanded": expanded,
              ref: this.menuButtonRefs[linkInfo.item],
            }}
            onClickHandler={() => {
              this._toggleMenu(linkInfo.item);
            }}
            active={menu === linkInfo.item}
          >
            {linkInfo.label}
          </BaseLink>
        </div>
      );
    });

    const externalLinks = externalLinkInfo.map((linkInfo, index) => {
      const key = `external-${index}-${linkInfo.label}`;
      return (
        <BaseLink
          key={key}
          className="ExternalLink"
          internalLinkComponent={internalLinkComponent}
          title={linkInfo.title}
          href={linkInfo.href}
          external={linkInfo.external}
        >
          {linkInfo.label}
        </BaseLink>
      );
    });

    const mainBar = (
      <nav className="NavigationBar">
        {logo}
        <a className="SkipToContentLink" href="#endOfNavbar">
          Skip to Content
        </a>
        <div className="NavigationCategory">{categoryLinks}</div>
        <div className="NavigationExternal">{externalLinks}</div>
      </nav>
    );

    const currentMenu = exiting || menu || entering;
    const section = sections[currentMenu] || {};

    const menuButton = section.buttonHidden ? null : (
      <div className="MenuButtonContainer">
        <BaseLink
          tabIndex="-1"
          internalLinkComponent={internalLinkComponent}
          href={section.buttonHref}
          external={section.buttonIsExternal}
        >
          <Button size="M" theme="Tertiary" className="MenuButton">
            {section.buttonText}
          </Button>
        </BaseLink>
      </div>
    );

    const dropdownMenu = (
      <CSSTransition
        classNames="MenuSlide"
        timeout={1000}
        key="Menu"
        in={this._isMenuPresent()}
        mountOnEnter
        unmountOnExit
        onEnter={() => {
          this._menuOpen(currentMenu);
        }}
        onEntered={this._menuOpened}
        onExit={() => {
          this._menuClose(currentMenu);
        }}
        onExited={this._menuClosed}
      >
        <div
          role="menu"
          tabIndex="0"
          className="NavigationMenu"
          ref={this.menuRefs[menu]}
          onKeyDown={this._handleOnKeyDown}
        >
          <div className="Content">
            <Heading size="S" className="MenuHeading">
              {section.subMenuTitle}
            </Heading>
            <BodyText size="L" className="MenuBodyText">
              {section.subMenuBodyText}
            </BodyText>
            {menuButton}
          </div>
          <div className="LinkFlexSection">
            <div className="LinkColumns">{section.columns}</div>
          </div>
        </div>
      </CSSTransition>
    );

    return (
      <div className={composedClassName}>
        {mainBar}
        {dropdownMenu}
        <div id="endOfNavbar" />
      </div>
    );
  }
}

export default NavigationBarDesktop;
