import React, { PureComponent } from "react";
import PropTypes from "prop-types";
import { BaseLink, Icon } from "../../atoms";
import { composeClassName } from "../../utilities/composeClassName/composeClassName";
import "./NavigationItemMobile.scss";

class NavigationItemMobile extends PureComponent {
  static propTypes = {
    className: PropTypes.string,
    itemKey: PropTypes.string.isRequired,
    external: PropTypes.bool,
    href: PropTypes.string,
    onClickHandler: PropTypes.func,
    children: PropTypes.any.isRequired,
  };

  render() {
    const {
      className,
      itemKey,
      external,
      href,
      onClickHandler,
      children,
    } = this.props;
    const composedClassName = composeClassName([
      "NavigationItemMobile",
      className,
    ]);

    let linkProps = { blank: true };
    if (external && href) {
      linkProps = { external: true, href: href };
    }
    return (
      <li className={composedClassName} key={itemKey}>
        <BaseLink {...linkProps} onClickHandler={onClickHandler}>
          <div>{children}</div>
          <Icon
            icon="chevronRight"
            size="S"
            className="NavigationItemCaret"
            defaultColor="DefaultGrey"
          />
        </BaseLink>
      </li>
    );
  }
}

export default NavigationItemMobile;
