import React, { PureComponent } from "react";
import PropTypes from "prop-types";
import { composeClassName } from "../../utilities";
import DisplayText from "../../atoms/DisplayText";
import Divider from "../../atoms/Divider";
import "./BulletList.scss";

class BulletList extends PureComponent {
  static propTypes = {
    className: PropTypes.string,
    /** The title at the top of the list */
    title: PropTypes.string,
  };

  static defaultProps = {
    className: "",
  };

  _renderTitleSection = (title) => {
    if (undefined !== title && title.length > 0) {
      return (
        <React.Fragment>
          <DisplayText size="M">{title}</DisplayText>
          <Divider className="BulletListDivider" />
        </React.Fragment>
      );
    }
    return "";
  };

  render() {
    const { title, children, className } = this.props;
    const composedClassName = composeClassName(["BulletList", className]);

    return (
      <div className={composedClassName}>
        {this._renderTitleSection(title)}
        <ul>{children}</ul>
      </div>
    );
  }
}

export default BulletList;
