import React, { PureComponent } from "react";
import PropTypes from "prop-types";

import { Button, DisplayText } from "../../atoms";
import { browser, composeClassName } from "../../utilities";

import "./ButtonVideo.scss";

class ButtonVideo extends PureComponent {
  static propTypes = {
    autoFocus: PropTypes.bool,
    buttonRef: PropTypes.shape({
      current: PropTypes.instanceOf(browser.Element),
    }),
    className: PropTypes.string,
    noDisplayText: PropTypes.bool,
    onClickHandler: PropTypes.func,
    tabIndex: PropTypes.string,
    theme: PropTypes.oneOf(["light", "dark"]),
  };

  static defaultProps = {
    onClickHandler: null,
    autoFocus: false,
    noDisplayText: false,
    tabIndex: "",
    theme: "light",
  };

  render() {
    const {
      className,
      buttonRef,
      onClickHandler,
      autoFocus,
      noDisplayText,
      tabIndex,
      theme,
    } = this.props;

    const buttonProps = {
      onClickHandler,
      autoFocus,
      tabIndex,
      buttonRef,
    };

    const composedClassName = composeClassName(["ButtonVideo", className]);
    const themeClass = theme === "dark" ? "Dark" : null;

    const playTriangle = (
      <svg
        className={composeClassName(["Icon", themeClass])}
        xmlns="http://www.w3.org/2000/svg"
        width="48"
        height="52"
        viewBox="0 0 16 16">
        <path
          fillRule="evenodd"
          strokeLinecap="round"
          strokeLinejoin="round"
          d="M6.972 4L.5 7.883V.117L6.972 4z"
        />
      </svg>
    );

    const displayText = noDisplayText ? null : (
      <DisplayText
        className={composeClassName(["DisplayText", themeClass])}
        centered
        size="M">
        watch the video
      </DisplayText>
    );

    return (
      <Button
        title={noDisplayText ? "Watch the Video" : ""}
        className={composedClassName}
        type="button"
        {...buttonProps}>
        <div className={composeClassName(["Circle", themeClass])}>
          {playTriangle}
        </div>
        {displayText}
      </Button>
    );
  }
}

export default ButtonVideo;
