import React, { PureComponent } from "react";
import PropTypes from "prop-types";
import { Container, BodyText, Icon } from "../../atoms";
import { allowedLocales } from "../../../config";
import LocaleSwitcher from "../LocaleSwitcher";

import "./LocaleSwitcherFirstTimeUser.scss";

class LocaleSwitcherFirstTimeUser extends PureComponent {
  static propTypes = {
    message: PropTypes.string,
  };

  static defaultProps = {
    message:
      "Select your country / language to see content specific to your location and shop online",
  };

  constructor(props) {
    super(props);
    this.state = {
      hidden: false,
    };
  }

  handleOnClick = (event) => {
    if (event.type === "click") {
      this.setState({ hidden: true });
    } else if (event.type === "keypress" && event.key === "Enter") {
      this.setState({ hidden: true });
    }
  };

  render() {
    const { hidden } = this.state;
    const { message } = this.props;

    return (
      <Container
        fluid
        className={`LocaleSwitcherFirstTimeUser ${hidden ? "Hidden" : ""}`}>
        <div className="LocaleSwitcherContainer">
          <BodyText size="S">{message}</BodyText>
          <LocaleSwitcher allowedLocales={allowedLocales} />
          <span
            role="button"
            tabIndex={0}
            onClick={this.handleOnClick}
            onKeyPress={this.handleOnClick}
            className="IconClose">
            <Icon icon="close" size="S" />
          </span>
        </div>
      </Container>
    );
  }
}

export default LocaleSwitcherFirstTimeUser;
