import React, { PureComponent } from "react";
import smoothscroll from "smoothscroll-polyfill";
import ButtonIcon from "../ButtonIcon";
import { composeClassName, browser } from "../../utilities";

import "./ButtonBackToTop.scss";

class ButtonBackToTop extends PureComponent {
  constructor(props) {
    super(props);
    this.state = { scrolled: false };
  }

  componentDidMount() {
    browser.window.onscroll = () => {
      this._handleOnScroll();
    };
  }

  _handleOnScroll = () => {
    if (
      browser.window.pageYOffset >=
      browser.document.documentElement.offsetHeight / 5
    ) {
      this.setState({ scrolled: true });
    } else {
      this.setState({ scrolled: false });
    }
  };

  _scrollToTop = () => {
    browser.window.scroll({ top: 0, left: 0, behavior: "smooth" });
    // above is not supported by safari, chrome or ie
    smoothscroll.polyfill();
  };

  render() {
    const { className } = this.props;
    const { scrolled } = this.state;

    let buttonSize = "L";
    if (browser.window.innerWidth < 1024) {
      buttonSize = "M";
    }
    if (browser.window.innerWidth < 768) {
      buttonSize = "S";
    }

    const composedClassName = composeClassName(["ButtonBackToTop", className]);

    let backToTop = null;

    if (scrolled) {
      backToTop = (
        <ButtonIcon
          className={composedClassName}
          size={buttonSize}
          theme="Primary"
          icon="chevronUp"
          title="Back To Top"
          tabIndex="-1"
          onClickHandler={() => {
            this._scrollToTop();
          }}
        />
      );
    }

    return <div className="PageContainer">{backToTop}</div>;
  }
}

export default ButtonBackToTop;
