import React, { PureComponent } from "react";
import PropTypes from "prop-types";
import { Icon } from "../../atoms";
import { InputDropdown } from "../../forms";
import { browser } from "../../utilities";

import "./LocaleSwitcher.scss";

class LocaleSwitcher extends PureComponent {
  static propTypes = {
    /** List of locales supported by the website, stored in config */
    allowedLocales: PropTypes.array,
  };

  constructor(props) {
    super(props);
    const { allowedLocales } = props;
    const pathnameLocale = browser.window.location.pathname.slice(1, 6);
    let locale;

    if (
      allowedLocales.filter((item) => {
        return item.value === pathnameLocale;
      }).length > 0
    ) {
      locale = browser.window.location.pathname.slice(1, 6);
    } else {
      locale = "en-CA";
    }

    this.state = {
      locale,
      allowedLocales,
      pathnameLocale,
    };
  }

  handleOnChange = (target) => {
    this.setState({ locale: target.value }, () => {
      const { locale, allowedLocales, pathnameLocale } = this.state;
      let redirectURL;

      // Check to see if the url already contains a locale, remove it if true

      if (
        allowedLocales.filter((item) => {
          return item.value === pathnameLocale;
        }).length > 0
      ) {
        redirectURL = `${locale}${browser.window.location.pathname.slice(6)}`;
      } else {
        redirectURL = `${locale}${browser.window.location.pathname}`;
      }

      browser.window.location.pathname = redirectURL;
    });
  };

  render() {
    const { locale, allowedLocales } = this.state;

    return (
      <div className="LocaleSwitcher">
        {/* TODO - change icon to be country icon */}
        <Icon icon="global" size="S" className="IconCountry" />
        <InputDropdown
          name="locale-switcher"
          value={locale}
          options={allowedLocales}
          onChangeHandler={this.handleOnChange}
        />
        <Icon icon="chevronDown" size="S" className="IconSelect" />
      </div>
    );
  }
}

export default LocaleSwitcher;
