import React, { PureComponent } from "react";
import PropTypes from "prop-types";

import { composeClassName, StringUtils } from "../../utilities";

import {
  Image,
  BaseLink,
  Col,
  Heading,
  DisplayText,
  BodyText,
} from "../../atoms";
import TextWithBackground from "../TextWithBackground";

import "./MarketingTileA.scss";

class MarketingTileA extends PureComponent {
  static propTypes = {
    imageSource: PropTypes.string.isRequired,
    imageAltText: PropTypes.string.isRequired,
    title: PropTypes.string.isRequired,
    backgroundColor: PropTypes.string,
    body: PropTypes.string,
    className: PropTypes.string,
    href: PropTypes.string,
    imageLongDesc: PropTypes.string,
    imageMobileSource: PropTypes.string,
    internalLinkComponent: PropTypes.any,
    linkTitle: PropTypes.string,
    mobile: PropTypes.bool,
    subTitle: PropTypes.string,
    tabIndex: PropTypes.string,
    whiteText: PropTypes.bool,
  };

  static defaultProps = {
    backgroundColor: "#6970b4",
    internalLinkComponent: null,
    mobile: false,
    whiteText: false,
  };

  render() {
    const {
      backgroundColor,
      body,
      subTitle,
      title,
      imageAltText,
      mobile,
      imageMobileSource,
      imageSource,
      imageLongDesc,
      external,
      href,
      linkTitle,
      internalLinkComponent,
      className,
      tabIndex,
      whiteText,
    } = this.props;

    const imageProps = {
      altText: imageAltText,
      longDesc: imageLongDesc,
      mobileSource: imageMobileSource,
      source: imageSource,
    };

    const text = (
      <TextWithBackground
        color={backgroundColor}
        whiteText={whiteText}
        className="TileText"
        key="marketing-tile-a-text">
        <DisplayText size="M" className="TileSubtitle">
          {StringUtils.formatMax(subTitle, 30)}
        </DisplayText>
        <Heading size="M" className="TileTitle">
          {StringUtils.formatMax(title, 30)}
        </Heading>
        <BodyText size="M" className="BodyText">
          {StringUtils.formatMax(body, 80)}
        </BodyText>
      </TextWithBackground>
    );
    const image = (
      <Image
        className="TileImage"
        key="marketing-tile-a-image"
        {...imageProps}
      />
    );

    let children = [text, image];
    if (mobile) {
      children = [image, text];
    }

    let childrenWithLink;
    if (href) {
      const linkProps = {
        href: href,
        internalLinkComponent: internalLinkComponent,
        title: linkTitle,
        external: external,
      };
      childrenWithLink = (
        <BaseLink className="TileLink" {...linkProps}>
          {children}
        </BaseLink>
      );
    }

    return (
      <Col
        className={composeClassName(["MarketingTileA", className])}
        xs={{ span: 12 }}
        md={{ span: 12 }}
        lg={{ span: 12 }}
        xl={{ span: 12 }}
        tabIndex={tabIndex}>
        {childrenWithLink || children}
      </Col>
    );
  }
}

export default MarketingTileA;
