import React, { PureComponent } from "react";
import PropTypes from "prop-types";
import ModalVideo from "../ModalVideo";
import {
  Image as ImageComponent,
  ImageBackground,
  DisplayText,
} from "../../atoms";
import "./Stories.scss";
import {
  StringUtils,
  composeClassName,
} from "../../utilities";

class Stories extends PureComponent {
  static propTypes = {
    className: PropTypes.string,
    Image: PropTypes.string.isRequired,
    AltText: PropTypes.string.isRequired,
    Longdesc: PropTypes.string,
    VideoSource: PropTypes.string,
    VideoId: PropTypes.string,
    VideoTitle: PropTypes.string,
    VideoButtonTheme: PropTypes.string,
    Tag: PropTypes.string.isRequired,
    ArticleTitle: PropTypes.string.isRequired,
    WhiteText: PropTypes.bool,
  };

  render() {
    const {
      className,
      Image,
      AltText,
      Longdesc,
      VideoSource,
      VideoId,
      VideoTitle,
      VideoButtonTheme,
      Tag,
      ArticleTitle,
      WhiteText,
    } = this.props;

    const ModalProps = {
      source: VideoSource,
      videoId: VideoId,
      title: VideoTitle,
      theme: VideoButtonTheme,
    };

    const storiesTextComponent = (
      <div className="StoriesTextWrapper">
        <DisplayText whiteText={WhiteText} size="S">{StringUtils.formatMax(Tag, 30)}</DisplayText>
        <DisplayText whiteText={WhiteText} size="L">{ArticleTitle}</DisplayText>
      </div>
    );

    const classNameList = [className, "Stories"];

    let storiesTileContent = (
      <div>
        <ImageComponent
          className="StoriesImage"
          source={Image}
          altText={AltText}
          longdesc={Longdesc}
        />
        {storiesTextComponent}
      </div>
    );

    if (VideoId && VideoTitle) {
      classNameList.push("WithVideo");

      storiesTileContent = (
        <div>
          <ImageBackground className="StoriesImage Video" source={Image}>
            <ModalVideo
              className="StoriesImage PlayButton"
              noDisplayText
              {...ModalProps}
            />
          </ImageBackground>
          {storiesTextComponent}
        </div>
      );
    }

    const composedClassName = composeClassName(classNameList);

    return <div className={composedClassName}>{storiesTileContent}</div>;
  }
}

export default Stories;
